import AuthenticationService from './../../services/AuthenticationService'

const mastermenurole = {
  namespaced: true,
  state: {
    datas: {},
    data: [],
    loading: false
  },
  getters: {
    getDatas (state) {
      return state.datas
    }
  },
  mutations: {
    SET_DATAS (state, data) {
      state.datas = data
    },
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async all ({ commit }, payload) {
      commit('SET_LOADING', true)
      return await AuthenticationService.get('menu-role/paginate?' + payload).then(({ data }) => {
        commit('SET_DATAS', data.data)
        commit('SET_DATA', data.data.data)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default mastermenurole
