import AuthenticationService from './../../services/AuthenticationService'
import Store from '@/store'

const attendances = {
  namespaced: true,
  state: {
    datas: {},
    data: [],
    loading: false,
    modalAttendanceDetail: false,
    detail: {},
    listDetail: {},
    listDetails: [],
    queryParam: null
  },
  getters: {
    getDatas (state) {
      return state.datas
    }
  },
  mutations: {
    SET_DATAS (state, data) {
      state.datas = data
    },
    SET_DATA (state, data) {
      state.data = data
    },
    SET_LIST_DETAIL (state, data) {
      state.listDetail = data
    },
    SET_LIST_DETAILS (state, data) {
      state.listDetails = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    },
    SET_MODAL_ATTENDANCE_DETAIL (state, bool) {
      state.modalAttendanceDetail = bool
    },
    SET_QUERY_PARAM (state, data) {
      state.queryParam = data
    }
  },
  actions: {
    onRefresh ({ state }) {
      Store.dispatch('attendances/paginate', state.queryParam)
    },
    async all ({ commit }, payload) {
    },
    async paginate ({ commit }, payload) {
      commit('SET_LOADING', true)
      commit('SET_QUERY_PARAM', payload)
      return await AuthenticationService.get('attendance/paginate?' + payload).then(({ data }) => {
        commit('SET_DATAS', data.data)
        commit('SET_DATA', data.data.data)
        commit('SET_LOADING', false)
      })
    },
    async onAction ({ state }, payload) {
      state.detail = payload[0]
      state.listDetails = []
      state.modalAttendanceDetail = true
    },
    async reqListDetail ({ commit }, payload) {
      commit('SET_LOADING', true)
      return await AuthenticationService.get('attendance/tmp/paginate?' + payload).then(({ data }) => {
        commit('SET_LIST_DETAIL', data.data)
        commit('SET_LIST_DETAILS', data.data.data)
        commit('SET_LOADING', false)
      })
    }
  }
}

export default attendances
