import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '',
    component: () => import('../layouts/App.vue'),
    meta: { requiresAuth: false },
    children: [
      {
        path: '/',
        name: 'IndexPage',
        component: () => import('../views/IndexView.vue')
      },
      {
        path: '/Terbaru',
        name: 'TerbaruPage',
        component: () => import('../views/IndexView.vue')
      },
      {
        path: '/Produk',
        name: 'ProdukPage',
        component: () => import('../views/IndexView.vue')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
